.footer {
     display: flex;
     justify-content: center;
     text-align: center;
     margin-top: 130px;
}

.footerContainer {
     display: flex;
     width: 90%;
     max-width: 1210px;
}

.footerLeft {
     flex: 35%;
     display: flex;
     flex-direction: column;
     align-items: flex-start;
     padding: 0px 20px 20px 20px;
}

.footerRight {
     flex: 65%;
     display: flex;
}

.footerLeftIcon {
     display: flex;
     align-items: center;
}

.footerLeftIconText {
     color: var(--color-white);
     font-size: 21px;
     font-weight: 600;
     margin-left: 5px;
}

.footerLeftText {
     color: var(--color-white);
     font-weight: 300;
     font-size: 17px;
     padding-top: 20px;
     opacity: 0.5;
}

.footerLeftLinks {
     width: 100%;
     display: flex;
     justify-content: flex-start;
     align-items: center;
}

.footerLeftLinksIcons {
     margin-top: 35px;
     display: flex;
     justify-content: space-between;
     max-width: 380px;
}

.footerLeftLinksIcon {
     width: 164px;
     height: 52px;
     display: flex;
     align-items: center;
     justify-content: center;
     background: #141819 0% 0% no-repeat padding-box;
     border-radius: 16px;
     margin-right: 26px;
     cursor: pointer;
}

.footerLeftLinksIcon:hover {
     transform: scale(1.03, 1.03);
}

.footerLeftLinksIcon:active {
     transform: scale(1.045, 1.045);
}

.footerLeftLinksIconLeft {
     flex: 35%;
     display: flex;
     align-items: center;
     justify-content: center;
}

.footerLeftLinksIconRight {
     flex: 65%;
     display: flex;
     flex-direction: column;
     align-items: flex-start;
}

.footerLeftLinksIconRightHeader {
     font: normal normal 200 7px Poppins;
     color: var(--color-white);
     opacity: 0.5;
}

.footerLeftLinksIconRightText {
     font: normal normal normal 13px Poppins;
     color: var(--color-white);
}

.footerLeftLinksIconIcon {
     width: 23px;
     height: auto;
}

.footerLeftC {
     color: var(--color-white);
     text-align: left;
     margin-top: 35px;
     margin-bottom: 20px;
     font-size: 18px;
     font-weight: 500;
}

.footerLeftCMobile {
     display: none;
}

.footerRightColumn {
     flex: 33%;
     margin: 10px 45px;
     display: flex;
     flex-direction: column;
     align-items: flex-start;
     text-align: left;
}

.footerRightTitle {
     color: var(--color-white);
     font-weight: 600;
     font-size: 30px;
     margin-bottom: 35px;
}

.footerRightLink {
     cursor: pointer;
     color: var(--color-white);
     margin-bottom: 25px;
     font-size: 16px;
     font-weight: 500;
     opacity: 0.5;
}

@media only screen and (max-width: 1400px) {
     .footerContainer {
          width: 95%;
     }
}

@media only screen and (max-width: 1000px) {
     .footerContainer {
          flex-direction: column;
          display: flex;
          justify-content: center;
     }

     .footerLeftC {
          display: none;
     }

     .footerLeftCMobile {
          display: block;
          color: var(--color-white);
          text-align: center;
          margin-top: 35px;
          margin-bottom: 20px;
          font-size: 19px;
          font-weight: 500;
     }

     .footerLeft {
          align-items: center;
     }

     .footerLeftLinks {
          justify-content: center;
          align-items: center;
     }
}

@media only screen and (max-width: 800px) {
     .footerRight {
          display: flex;
          flex-direction: column;
     }
     .footerLeftIcon {
          margin-left: -15px;
     }

     .footerRightColumn {
          justify-content: center;
          align-items: center;
          margin: 10px 0;
     }

     .footerLeftLinksIcon {
          margin-right: 0;
     }

     .footerLeftLinksIcons {
          width: 95%;
     }
}
